body {
  font-family: Arial, sans-serif;
  margin: 1rem;
}
.container {
  max-width: 700px;
  margin: auto;
}
.row {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
}
.input {
  flex-grow: 1;
  padding: 0.5rem;
  font-size: 1rem;
}
.filters {
  margin-bottom: 1rem;
}
.filters select,
.filters input {
  margin-right: 0.5rem;
  padding: 0.3rem;
  font-size: 1rem;
}
.products {
  list-style: none;
  padding: 0;
}
.products li {
  border-bottom: 1px solid #ddd;
  padding: 0.5rem 0;
}
